import classes from "./ProjectCard.module.css";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/opacity.css";
import "react-lazy-load-image-component/src/effects/blur.css";

const ProjectCard = ({ title, description, image, repoName }) => {
    const link = `https://github.com/mayanksetia13/${repoName}`;

    return (
        <div className={classes.card}>
            {/* <img className={classes.card_img} src={image} alt={title} /> */}
            <LazyLoadImage
                className={classes.card_img}
                alt={title}
                src={image}
                effect="blur"
                threshold={100}
                placeholderSrc={image}
            />
            <a href={link}>
                <div className={classes.card_content}>
                    <h3 className={classes.card_title}>{title}</h3>
                    <p className={classes.card_description}>{description}</p>
                </div>
            </a>
        </div>
    );
};

export default ProjectCard;
