import classes from "./Projects.module.css";
import ProjectCard from "./ProjectCard";

const Projects = (props) => {
    return (
        <div className={classes.project_section}>
            <div className={classes.projects_intro}>
                <h2 className={classes.project_intro_heading}>Projects</h2>
                <span className={classes.project_intro_span}>
                    I've been working on
                </span>
            </div>
            <div className={classes.card_section}>
                {props.projectsData.map((p) => {
                    return (
                        <ProjectCard
                            key={p.id}
                            title={p.title}
                            description={p.description}
                            image={p.image}
                            repoName={p.repoName}
                        />
                    );
                })}
            </div>
        </div>
    );
};

export default Projects;
